<template>
  <div class="pt-6 pb-6 bg-gray-100">
    <div id="book">
      <!-- container -->
      <div class="container w-100 lg:w-4/5 mx-auto flex flex-col">
        <!-- book -->
        <div class="flex justify-between items-center">
          <div class="flex">
            <div class="pl-4 font-serif uppercase text-4xl xl:text-3xl">
              RESULTS
            </div>
            <h2
              class="pl-4 rounded-lg font-serif uppercase text-4xl xl:text-3xl bg-gray-400"
            >
              {{ getBookLen() }}
            </h2>
          </div>

          <add-form class="pr-4" @submitBook="loadAllBooks" ref="edit"></add-form>
        </div>

        <div
          v-for="book in books.slice((currentPage-1)*pageSize,currentPage*pageSize)"
          :key="book"
          class="flex flex-col md:flex-row overflow-hidden bg-white rounded-lg shadow-xl my-8 w-100 mx-2"
        >
          <!-- image -->
          <div class="h-auto w-auto md:w-2/3">
            <img
              class="inset-0 h-full w-full object-cover object-center"
              :src="book.cover"
            />
          </div>

          <!-- content -->
          <div
            class="w-full py-4 px-6 text-gray-800 flex flex-col justify-start"
          >
            <div class="flex justify-between">
              <h3
                class="font-semibold text-xl text-center leading-tight truncate"
              >
                {{ book.title }}
              </h3>

              <div>
                <el-button
                  style="outline: none"
                  class="item-center"
                  size="small"
                  type="primary"
                  icon="el-icon-edit"
                  @click="editBook(book)"
                  circle
                ></el-button>
                <el-button
                  style="outline: none"
                  class="item-center"
                  type="danger"
                  size="small"
                  icon="el-icon-delete"
                  circle
                  @click="deleteBook(book.id)"
                ></el-button>
              </div>
            </div>

            <p class="mt-2 text-sm text-gray-700 tracking-wide font-semibold">
              作者: {{ book.author }}
            </p>

            <p class="pt-4">
              <pre style="white-space: pre-wrap;">{{ book.abs }}</pre>
            </p>

            <p
              class="mt-2 text-sm text-gray-700 tracking-wide font-normal text-right"
            >
              {{ book.referrer }} &bull; {{ book.date }}
            </p>
          </div>
        </div>

        <div class="flex justify-center">
          <el-pagination
            :background="true"
            layout="prev, pager, next"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="pageSize"
            :total="books.length">
          </el-pagination>
        </div>


      </div>

    </div>
  </div>
</template>

<script>
import AddForm from "./AddForm.vue";
export default {
  components: {
    AddForm,
  },
  data() {
    return {
      books: [],
      booklen: 0,
      currentPage: 1,
      pageSize: 3,
    };
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
    },
    getBookLen() {
      return this.booklen;
    },

    //加载所有cards
    loadAllBooks() {
      var _this = this;
      this.$axios.get("/books").then((resp) => {
        if (resp && resp.status === 200) {
          _this.books = resp.data;
          console.log("reload all books");
          this.booklen = this.books.length;
        }
      });
    },

    //根据关键字查询 （书名/作者）
    searchInput(keywords) {
      var _this = this;
      this.$axios.get("/search?keywords=" + keywords).then((resp) => {
        if (resp && resp.status === 200) {
          _this.books = resp.data;
          this.booklen = this.books.length;
          console.log("search by keywords success!");
        }
      });
    },

    //根据书籍id删除
    deleteBook(id) {
      this.$axios.post("/delete", { id: id }).then((resp) => {
        if (resp && resp.status === 200) {
          console.log("delete success!");
          this.loadAllBooks();
        }
      });
    },
    //编辑书籍
    editBook(book) {
      console.log(book.title);
      this.$refs.edit.dialogFormVisible = true;
      this.$refs.edit.form = {
        id: book.id,
        title: book.title,
        author: book.author,
        referrer: book.referrer,
        cover: book.cover,
        abs: book.abs,
        date: book.date,
      };
    },
  },

  //渲染页面前加载所有cards
  mounted() {
    this.loadAllBooks();
    console.log("loadAllBooks!!!");
  },
};
</script>

<style>
.button-outline-none {
  outline: none;
}
</style>