<template>
  <header class="bg-opacity-10 mycontainer" x-data="{ isOpen: false }">
    <nav
      class="container px-6 py-4 mx-auto md:flex md:justify-between md:items-center"
    >
      <div class="flex items-center justify-between">
        <a
          class="text-xl font-bold text-gray-200 transition-colors duration-300 transform md:text-2xl hover:text-indigo-400"
          href="#"
          >Reader Home
        </a>

        <!-- Mobile menu button -->
        <div @click="isOpen = !isOpen" class="flex md:hidden">
          <button
            type="button"
            class="text-gray-200 hover:text-gray-400 focus:outline-none focus:text-gray-400"
            aria-label="toggle menu"
          >
            <svg viewBox="0 0 24 24" class="w-6 h-6 fill-current">
              <path
                fill-rule="evenodd"
                d="M4 5h16a1 1 0 0 1 0 2H4a1 1 0 1 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2z"
              ></path>
            </svg>
          </button>
        </div>
      </div>

      <!-- Mobile Menu open: "block", Menu closed: "hidden" -->
      <div
        :class="isOpen ? 'flex' : 'hidden'"
        class="flex-col mt-2 space-y-4 md:flex md:space-y-0 md:flex-row md:items-center md:space-x-10 md:mt-0"
      >
        <a
          class="text-sm font-medium text-gray-200 transition-colors duration-300 transform hover:text-indigo-400"
          href="#"
          >Link</a
        >
        <a
          class="pr-5 text-sm font-medium text-gray-200 transition-colors duration-300 transform hover:text-indigo-900"
          href="#"
          >Help</a
        >
      </div>
    </nav>

    <!-- search section -->
    <section class="flex-col items-center justify-center" style="height: 400px">
      <div class="text-center text-4xl pt-32 text-gray-100">Reader.Dev</div>

      <div class="text-center mt-7 w-full">
        <div
          class="w-full mx-auto rounded-xl opacity-80 shadow-lg p-10 text-gray-800 relative overflow-hidden resize-x min-w-80 max-w-3xl"
        >
          <div class="relative mt-1">
            <input
              type="text"
              v-model="keywords"
              @keydown="notifyChild"
              class="w-full pl-3 pr-10 py-2 border-2 border-gray-200 rounded-xl hover:border-gray-300 focus:outline-none focus:border-indigo-500 focus:text-gray-800 transition-colors"
              placeholder="Search..."
            />
            <button
              @click="notifyChild" class="block w-7 h-7 text-center text-xl leading-0 absolute top-2 right-2 text-gray-400 focus:outline-none hover:text-gray-900 transition-colors"
            >
              <svg
                class="hover text-pink-600"
                xmlns="http://www.w3.org/2000/svg"
                xmlns:xlink="http://www.w3.org/1999/xlink"
                aria-hidden="true"
                focusable="false"
                width="1em"
                height="1em"
                style="
                  -ms-transform: rotate(360deg);
                  -webkit-transform: rotate(360deg);
                  transform: rotate(360deg);
                "
                preserveAspectRatio="xMidYMid meet"
                viewBox="0 0 24 24"
              >
                <path
                  d="M9.5 3A6.5 6.5 0 0 1 16 9.5c0 1.61-.59 3.09-1.56 4.23l.27.27h.79l5 5l-1.5 1.5l-5-5v-.79l-.27-.27A6.516 6.516 0 0 1 9.5 16A6.5 6.5 0 0 1 3 9.5A6.5 6.5 0 0 1 9.5 3m0 2C7 5 5 7 5 9.5S7 14 9.5 14S14 12 14 9.5S12 5 9.5 5z"
                  fill="#626262"
                />
              </svg>
            </button>
          </div>
          <div class="absolute top-0 left-0 w-full h-2 flex">
            <div class="h-2 bg-blue-500 flex-1"></div>
            <div class="h-2 bg-red-500 flex-1"></div>
            <div class="h-2 bg-yellow-500 flex-1"></div>
            <div class="h-2 bg-blue-500 flex-1"></div>
            <div class="h-2 bg-green-500 flex-1"></div>
            <div class="h-2 bg-red-500 flex-1"></div>
          </div>
        </div>
      </div>

      <div class="text-center text-gray-100">
        Recommend great books or polish others.
      </div>
    </section>
  </header>

  <Card ref="child"></Card>
</template>

<script>
import Card from "./Card.vue";
export default {
  components: {
    Card,
  },
  data() {
    return {
      keywords: ''
    };
  },
  methods: {
    notifyChild() {
      if (this.keywords != '') {
        this.$refs.child.searchInput(this.keywords)
      }
    }
  }
};
</script>
<style>
.mycontainer {
  background-image: linear-gradient(rgba(0, 0, 0, 0.527), rgba(0, 0, 0, 0.247)),
    url("~@/assets/books.png");
  background-size: cover;
}
</style>