<template>
  <!-- Add button-->
  <div>
    <button
      @click="dialogFormVisible = true"
      style="outline: none"
      class="bg-gradient-to-r from-green-400 to-blue-500 hover:from-pink-500 hover:to-yellow-500 text-gray-200 font-bold py-2 px-4 rounded-lg inline-flex items-center border-0"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="h-8 w-8 text-gray-800 items-center"
        viewBox="0 0 20 20"
        fill="currentColor"
      >
        <path
          fill-rule="evenodd"
          d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-11a1 1 0 10-2 0v2H7a1 1 0 100 2h2v2a1 1 0 102 0v-2h2a1 1 0 100-2h-2V7z"
          clip-rule="evenodd"
        />
      </svg>
      <span class="text-gray-800"> Add One </span>
    </button>

    <el-dialog title="添加推荐书籍" v-model="dialogFormVisible">
      <!-- 表单 -->
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="书籍名" :label-width="formLabelWidth" prop="title">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="作者" :label-width="formLabelWidth" prop="author">
          <el-input v-model="form.author" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item
          label="推荐人"
          :label-width="formLabelWidth"
          prop="referrer"
        >
          <el-input v-model="form.referrer" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item
          label="介绍/评论"
          :label-width="formLabelWidth"
          prop="abs"
        >
          <el-input
            type="textarea"
            v-model="form.abs"
            :rows="7"
            maxlength="300"
            autocomplete="off"
            show-word-limit="true"
          >
          </el-input>
        </el-form-item>

        <el-form-item label="插图" :label-width="formLabelWidth" prop="cover">
          <el-input
            v-model="form.cover"
            autocomplete="off"
            placeholder="Image URL"
          ></el-input>
          <upload-image @onLoadImage="getImageUrl" ref="upload"></upload-image>
        </el-form-item>
      </el-form>

      <template #footer>
        <span class="dialog-footer">
          <el-button
            @click="
              dialogFormVisible = false;
              clearForm();
            "
            >取 消</el-button
          >
          <el-button type="primary" @click="submitBook('form')"
            >确 定</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import UploadImage from "./UploadImage.vue";
export default {
  components: {
    UploadImage,
  },
  data() {
    return {
      dialogFormVisible: false,
      form: {
        id: "",
        title: "",
        author: "",
        referrer: "",
        cover: "",
        abs: "",
        date: "",
      },
      rules: {
        title: [
          { required: true, message: "请输入推荐书籍名", trigger: "blur" },
        ],
        author: [{ required: true, message: "请输入作者名", trigger: "blur" }],
        referrer: [
          { required: true, message: "请输入推荐人昵称", trigger: "blur" },
        ],
        abs: [
          { required: true, message: "请输入图书简介", trigger: "blur" },
          {
            min: 100,
            max: 300,
            message: "请输入至少100-300字之间",
            trigger: "blur",
          },
        ],
        cover: [{ required: true, message: "请选择一张插图", trigger: "blur" }],
      },
      formLabelWidth: "100px",
      remnant: 300,
    };
  },
  methods: {
    absInput() {
      var pVal = this.abs.length;
      this.remnant = 300 - pVal;
    },
    //从UploadImage.vue获取从服务器返回的图片url
    getImageUrl(url) {
      console.log("getImageUrl" + url);
      this.form.cover = url;
    },
    // 提交添加/修改推荐书籍表单
    submitBook(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          console.log("submitbook!");
          var today = new Date();
          this.form.date =
            today.getFullYear() +
            "-" +
            (today.getMonth() + 1) +
            "-" +
            today.getDate();
          this.$axios
            .post("/books", {
              id: this.form.id,
              title: this.form.title,
              cover: this.form.cover,
              author: this.form.author,
              abs: this.form.abs,
              referrer: this.form.referrer,
              date: this.form.date,
            })
            .then((resp) => {
              if (resp && resp.status === 200) {
                console.log("submitBook status 200");
                this.dialogFormVisible = false;
                this.$refs.upload.clearImage();
                this.$emit("submitBook");
                this.clearForm();
              }
            });
        } else {
          console.log("error submit");
        }
      });
    },
    //清除表单内容
    clearForm() {
      this.form.title = "";
      this.form.author = "";
      this.form.cover = "";
      this.form.referrer = "";
      this.form.abs = "";
      this.form.id = "";
      this.form.date = "";
      this.$refs.form.clearValidate();
    },
  },
};
</script>

