<template>
  <el-upload
    ref="upload"
    action="http://49.235.89.66:8443/api/covers"
    :on-preview="handlePreview"
    :on-remove="handleRemove"
    :on-success="handleSuccess"
    :before-upload="beforImageUpload"
    :file-list="fileList"
    list-type="picture"
  >
    <el-button size="small" type="primary"
      >点击上传</el-button
    >

    <template #tip>
      <div class="el-upload__tip">只能上传 jpg/png 文件，且不超过4MB</div>
    </template>
  </el-upload>
</template>


<script>
export default {
  data() {
    return {
        fileList: [],
        url: ''
    };
  },
  methods: {
    //上传前限制图片大小和图片格式
    beforImageUpload(file) {
      const isJPG = (file.type === 'image/jpeg' || file.type === 'image/png');
        const isLt2M = file.size / 1024 / 1024 < 4;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG/PNG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 4MB!');
        }
        return isJPG && isLt2M;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleSuccess(response) {
        this.url = response
        this.$emit("onLoadImage", this.url);
        this.$message.warning('图片上传成功')
    },
    clearImage() {
      this.$refs.upload.clearFiles()
    }
  },
};
</script>

